.nav {
  color: $white-dark;
  ul {
    @include flex; }
  a {
    display: block;
    padding-top: 25px;
    padding-bottom: 25px;
    text-transform: uppercase;
    @include tr(color .3s, background-color .3s);
    padding-left: 25px;
    padding-right: 40px;
    &.is-active {
      color: $white;
      background-color: #1f1f1f; }
    @include hover {
      color: $white;
      background-color: #1f1f1f; } } }
