.hero {
  position: relative;
  &__content {
    max-width: 630px; }
  &__title, &__btns {
    margin-bottom: 25px;
    @include md {
      margin-bottom: 15px; } }
  &__text {
    margin-bottom: 50px;
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 15px; } }
  &__btns {
    @include flex; }
  &__btn {
    @include notlast {
      margin-right: 15px; } }
  &__del {
    color: $white;
    .icon {
      font-size: 45px;
      margin-right: 10px;
      vertical-align: middle; } }


  .hero-slide {
    @include cover;
    &__inner {
      @include flex(center, normal, column);
      padding-top: 10%;
      padding-bottom: 12%; } } }
