@include font('opensans-regular', 'opensans-regular-webfont');
@include font('opensans-bold', 'opensans-bold-webfont');
@include font('opensans-semibold', 'opensans-semibold-webfont');
@include font('opensans-light', 'opensans-light-webfont');
@include font('opensans-regularitalic', 'opensans-regularitalic-webfont');
@include font('opensans-lightitalic', 'opensans-lightitalic-webfont');
@include font('opensans-bolditalic', 'opensans-bolditalic-webfont');
@include font('opensans-semibolditalic', 'opensans-semibolditalic-webfont');

@mixin reg {
  font-family: 'opensans-regular', Arial, sans-serif; }
@mixin bold {
  font-family: 'opensans-bold', Arial, sans-serif; }
@mixin semi {
  font-family: 'opensans-semibold', Arial, sans-serif; }
@mixin semiit {
  font-family: 'opensans-semibolditalic', Arial, sans-serif; }
@mixin italic {
  font-family: 'opensans-regularitalic', Arial, sans-serif; }
@mixin light {
  font-family: 'opensans-light', Arial, sans-serif; }
@mixin lightit {
  font-family: 'opensans-lightitalic', Arial, sans-serif; }
@mixin boldit {
  font-family: 'opensans-bolditalic', Arial, sans-serif; }
@mixin georgia {
  font-family: 'Georgia', serif; }
