body {
  background: $white;
  line-height: 1.4;
  font-size: $fz;
  min-width: 320px;
  @include reg;
  color: $c-text;
  @include no-scroll; }

input, textarea {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  @include placeholder {
    font-family: inherit;
    font-size: inherit; } }

button {
  font-size: inherit;
  font-family: inherit;
  color: inherit; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

.container {
  max-width: 1440px;
  padding-left: 15px;
  padding-right: 15px;
  margin-left: auto;
  margin-right: auto; }

.yellow {
  color: $yellow !important; }
.accent {
  color: $accent !important; }
.overflow-hidden {
  overflow: hidden !important; }
.bg-top-right {
  background-position: 100% 0% !important; }
.bg-cover {
  background-size: cover !important; }
.bg-center-center {
  background-position: 50% 50%; }


.price {
  @include bold;
  color: $white-dark;
  font-size: 24px;
  line-height: 1;
  small {
    font-size: 0.55em; }
  &--red {
    color: $accent; }
  &--gray-dark {
    color: $gray-dark; }
  &--sm {
    font-size: 14px; }
  &--old {
    position: relative;
    @include after {
      width: 100%;
      top: 50%;
      transform: translate(0, -50%);
      left: 0;
      position: absolute;
      height: 1px;
      background-color: currentColor; } } }

.counter {
  $this: &;
  color: $gray-light;
  @include flex(center, center);
  input, button {
    height: 42px;
    background-color: transparent;
    text-align: center; }
  input {
    width: 60px;
    padding: 5px 10px; }
  button {
    font-size: 1.5em;
    line-height: 42px;
    width: 50px;
    &:first-child {
      border-right: 1px solid $c-border;
      border-top-left-radius: 20px;
      border-bottom-left-radius: 20px; }
    &:last-child {
      border-left: 1px solid $c-border;
      border-top-right-radius: 20px;
      border-bottom-right-radius: 20px; } }
  &__inner {
    background-color: $black;
    border-radius: 20px;
    @include flex(center, center); }
  &--white {
    input, button {
 }      // background-color: $white
    #{$this}__inner {
      background-color: $white;
      box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.17); } } }

.border {
  padding: 18px;
  // border-radius: 100px
  // border: 1px solid $c-border
  position: relative;
  color: $gray;
  @include image('border-sm.png');
  @include cover;
  background-size: 100%;
  &--sm-dark {
    @include image('border-sm-dark.png'); } }

.quote-fu {
  position: relative;
  @include image('border-title.png');
  @include cover;
  background-size: 100%;
  @include lightit;
  padding: 90px 40px;
  background-color: $white;
  border-radius: 120px;
  .icon {
    fill: $accent;
    position: absolute;
    font-size: 50px;
    bottom: -10px;
    right: 170px; } }

.quote {
  position: relative;
  background-color: $white;
  @include lightit;
  border-left: 3px solid $accent;
  padding: 30px;
  box-shadow: 0px 3px 57px 0px rgba(0, 0, 0, 0.11);
  p {
    @include notlast {
      margin-bottom: 30px;
      @include md {
        margin-bottom: 15px; } } } }

.gallery-slide {
  @include cover;
  border-radius: 15px;
  padding-top: 40%;
  display: block; }




